<template>
  <div class="filter">
    <el-input
      @input="filter"
      placeholder="Search Images"
      v-model="filterBy.title"
    />
  </div>
</template>

<script>
import { utilService } from '../services/util.service.js';
export default {
  data() {
    return {
      filterBy: {
        title: '',
      },
    };
  },
  created() {
    this.filter = utilService.debounce(this.filter, 2000);
  },
  methods: {
    filter() {
      this.$store.dispatch({ type: 'filter', filterBy: { ...this.filterBy } });
    },
  },
  components: {},
};
</script>

<style></style>
