import { renderSync } from 'sass';
import { utilService } from './util.service';
import { storageService } from './async-storage-service';

const Axios = require('axios');
// import Axios from 'axios'
var axios = Axios.create({ withCredentials: true });

const KEY = 'imagesDB';

export const imageService = {
  query,
  getById,
  deleteImage,
  save,
  getEmptyImage,
};

function _getUrl(id = '') {
  const BASE_URL =
    process.env.NODE_ENV !== 'development'
      ? '/api/image'
      : '//localhost:3030/api/image';
  return `${BASE_URL}/${id}`;
}

async function query(filterBy) {
  try {
    let images = await storageService.query(KEY);
    let imagesToShow = [];
    if (!images || images.length === 0) {
      images = await axios.get('https://jsonplaceholder.typicode.com/photos');
      imagesToShow = images.data.splice(0, 50);
    } else {
      imagesToShow = images.splice(0, 50);
    }
    imagesToShow.forEach((image) => {
      image.url = `https://source.unsplash.com/${utilService.getRandomInt(
        150,
        400
      )}x${utilService.getRandomInt(150, 400)}`;
    });
    localStorage.setItem(KEY, JSON.stringify(imagesToShow));
    const filteredImages = filterImages(imagesToShow, filterBy);
    return filteredImages;
  } catch (err) {
    console.log(err);
  }
}

function filterImages(imagesToShow, filterBy) {
  let filteredImages;
  if (filterBy) {
    filteredImages = imagesToShow.filter((image) =>
      image.title.includes(filterBy.title.toLowerCase())
    );
  } else {
    filteredImages = imagesToShow;
  }
  return filteredImages;
}

async function getById(imageId) {
  try {
    const res = await storageService.get(KEY, imageId);
    return res;
  } catch (err) {
    console.log(err);
  }
}

async function deleteImage(imageId) {
  try {
    const res = await axios.delete(_getUrl(imageId));
    return res.data;
  } catch (err) {
    console.log(err);
  }
}

async function save(image) {
  try {
    if (!image._id) {
      const addedImage = await addImage(image);
      return addedImage;
    } else {
      const updatedImage = await updateImage(image);
      return updatedImage;
    }
  } catch (err) {
    console.log(err);
  }
  // const savedImage = image._id ? updateImage(image) : addImage(image)
  // return savedImage
}

async function updateImage(updateImage) {
  try {
    const res = await axios.put(_getUrl(updateImage._id), updateImage);
    return res.data;
  } catch (err) {
    console.log(err);
  }
}

async function addImage(image) {
  try {
    const res = await axios.post(_getUrl(), image);
    console.log(res.data);
    return res.data;
  } catch (err) {
    console.log(err);
  }
}

function getEmptyImage() {
  return {
    name: '',
    price: null,
    labels: [],
    createdAt: null,
    inStock: null,
    url: 'imgs/woodi.png',
  };
}
