import { createStore } from 'vuex';
import { imageService } from '../services/image-service';

export default createStore({
  state: {
    images: [],
    filterBy: '',
  },
  getters: {
    imagesForDisplay(state) {
      return state.images;
    },
  },
  mutations: {
    setImages(state, { images }) {
      state.images = images;
    },
    filter(state, { filterBy }) {
      state.filterBy = filterBy;
    },
  },
  actions: {
    filter({ commit, dispatch }, { filterBy }) {
      commit({ type: 'filter', filterBy });
      dispatch({ type: 'loadImages' });
    },
    async loadImages({ commit, state }) {
      try {
        var filterBy = state.filterBy ? state.filterBy : '';
        const images = await imageService.query(filterBy);
        commit({ type: 'setImages', images });
      } catch (err) {
        console.log(err);
      }
    },
  },
  modules: {},
});
