<template>
  <div @mouseover="isHovered = true" @mouseleave="isHovered = false">
    <div @click="openDetails(image.id)" class="image-preview">
      <h3 v-if="isHovered">{{ image.title }}</h3>
      <img :src="image.url" />
    </div>
  </div>
  <!-- <button @click="removeImage(image._id)">X</button> -->
</template>

<script>
export default {
  props: ['image', 'src'],
  data() {
    return {
      isHovered: false,
    };
  },
  created() {
    // console.log('image:', this.image)
  },
  methods: {
    openDetails(id) {
      this.$router.push(`/image/details/${id}`);
    },
    removeImage() {
      this.$emit('removeImage');
    },
  },
  computed: {},
};
</script>

