<template>
  <section class="image-container">
    <div>
      <Filter />
      <div v-if="isLoading" class="screen">
        <h1>Loading Gallery...</h1>
        <div class="circle-loader"></div>
      </div>
      <image-list @remove="remove" :images="imagesForDisplay"></image-list>
    </div>
  </section>
</template>

<script>
import imageList from '../components/ImageList.vue';
import Filter from '../components/Filter.vue';

export default {
  data() {
    return {
      images: [],
      isLoading: true,
    };
  },
  mounted() {
    setTimeout(() => (this.isLoading = false), 4000);
  },
  components: {
    imageList,
    Filter,
  },
  created() {
    this.$store.dispatch({ type: 'loadImages' });
  },
  computed: {
    imagesForDisplay() {
      return this.$store.getters.imagesForDisplay;
    },
  },
  methods: {
    remove(imageId) {
      this.$store.dispatch({ type: 'remove', imageId }).then(() => {});
    },
  },
};
</script>

