<template>
  <section v-if="image" class="image-details bounce-in-top container">
    <h1>{{ image.title }}</h1>
    <img :src="image.url" alt="" />
    <el-button @click="backToMain">Back To Gallery</el-button>
  </section>
</template>

<script>
import { imageService } from '../services/image-service.js';
export default {
  data() {
    return {
      image: null,
    };
  },
  created() {
    this.loadImage();
  },
  computed: {},
  methods: {
    async loadImage() {
      try {
        // move to store
        const id = this.$route.params.imageId;
        const image = await imageService.getById(+id);
        this.image = image;
      } catch (err) {
        console.log(err);
      }
    },
    backToMain() {
      this.$router.push('/');
    },
  },
  components: {},
};
</script>
